<template>
  <div>
    <pre v-if="user">{{ user }}</pre>
    <router-link v-if="currentUser" to="/settings" class="button is-link">Edit profile</router-link>
    <div>
      <router-link :to="{name: 'ProfileAuthor', params: {name: userName}}">Author</router-link>
      <router-link :to="{name: 'ProfileFavorited', params: {name: userName}}">Favorited</router-link>
    </div>
    <router-view />
  </div>
</template>

<script>
import Api from '@/api/common'
import { mapGetters } from 'vuex'

export default {
  name: 'Profile',
  data () {
    return {
      user: null
    }
  },
  computed: {
    userName () {
      return this.$route.params.name
    },
    currentUser () {
      if (this.userProfile && this.user) {
        return this.userProfile.username === this.user.username
      }
    },
    ...mapGetters({
      userProfile: 'user/userDetails'
    })
  },
  created () {
    this.fetchUser(this.userName)
  },
  watch: {
    userName (name) {
      this.fetchUser(name)
    }
  },
  methods: {
    fetchUser (name) {
      Api.getProfile(name)
        .then(({data}) => {
          if (data.profile) {
            this.user = data.profile
          }
        })
    }
  }
}
</script>
